import { useState, useEffect } from "react";

export const useValidation = (value, validations) => {
    const [isEmpty, setEmpty] = useState(true)
    const [minLengthError, setMinLengthError] = useState(false)
    const [maxLengthError, setMaxLengthError] = useState(false)
    const [isMatchesRegExp, setMatchesRegExpError] = useState(false)
    const [inputValid, setInputValid] = useState(false)
    const [inputError, setInputError] = useState([])

    useEffect(() => {

        const fieldsError = []

        for (const validation in validations) {
            switch(validation) {
                case 'isEmpty':
                    if(value) {
                        setEmpty(false)
                    } else {
                        setEmpty(true)
                        fieldsError.push("    ")
                    }
                    break;
                case 'minLength':
                    if(value.length < validations[validation]) {
                        setMinLengthError(true)
                        fieldsError.push(`    ${validations[validation]} `)
                    } else {
                        setMinLengthError(false)
                    }
                    break;
                case 'maxLength':
                    if(value.length > validations[validation]) {
                        setMaxLengthError(true)
                        fieldsError.push(`     ${validations[validation]} `)
                    } else {
                        setMaxLengthError(false)
                    }
                    break;
                case 'pattern':
                    let regExp = new RegExp(validations[validation])
                    if(value.match(regExp) == null) {
                        setMatchesRegExpError(true)
                        fieldsError.push("    ")
                    } else {
                        setMatchesRegExpError(false)
                    }
                    break;
                default:
                    break;
            }
        }

        setInputError(fieldsError)
        console.log(inputError)

    }, [value])

    useEffect(() => {

        if(isEmpty || minLengthError || maxLengthError || isMatchesRegExp) {
            setInputValid(false)
        } else {
            setInputValid(true)
        }
    }, [isEmpty, minLengthError, maxLengthError, isMatchesRegExp])


    return {
        isEmpty,
        minLengthError,
        maxLengthError,
        isMatchesRegExp,
        inputValid,
        inputError
    }
}